@mixin normalize-list() {
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style-type: none;
}

@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
